.footer__inner{
    height: 45vw;
    background-color: #6ab04c;
    color: #fff !important;
    .footer__top{
        padding: 6vw 0;
        grid-template-columns: repeat(4, 1fr);
        gap: 4vw;
        height: 27vw;
        a > div{
            margin: 0.8vw 0;
            color: #fff !important;
        }
        .footer__top__element-1{
            
            display: grid;
            row-gap: 3vw;
        }
        .footer__top__element-2{
            display: grid;
            row-gap: 1.2vw;
        }
    }
    .footer__text{
        font-size: 0.9rem;
        color: #fff;
    }
    .footer__text__description{
        cursor: pointer;
        font-size: 0.8rem;
        font-weight: 200;
        margin-top: 0.8vw;
    }
    .footer__bot,
    .footer__top{ 
        margin: 0 12vw;
        align-items: baseline;
        display: grid;
    }
    .footer__bot{
        grid-template-columns: repeat(3, 1fr);
    }

}

@media (max-width: 767px) {
    .footer__bot{
        height: max-content;
        position: relative;
        display: grid;
        grid-row-gap: 3vw;
        margin: 2rem 0!important;
        grid-template-columns: auto !important;
    }
    .footer__top{
        margin: 0 !important;
        grid-template-columns: repeat(2, 1fr) !important;
    }
    .footer__inner{
        padding: 0 2vw !important;
        display: grid !important;
        grid-template-columns: 3fr 1fr !important;
    }
        .footer__top__element-2{
            display: none !important;
        }

}

@media (max-width: 320px) {
    .footer__bot{
        top: 0 !important;
        height: max-content !important;
        position: relative;
        display: grid;
        margin: 0 !important;
        grid-template-columns: 1fr !important;
        padding-bottom: 1rem;
    }
    .footer__top{
        height: max-content !important;
        margin: 0 !important;
        grid-template-columns: 1fr !important;
    }
    .footer__inner{
        margin-bottom: -3rem !important;
        height: max-content;
        padding: 0 2vw !important;
        display: grid !important;
        grid-template-columns: 1fr !important;
    }
        .footer__top__element-2{
            display: none !important;
        }

}