$size__item: 28rem;
.slider__inner {
    user-select: none;
    max-width: 90vw;
    background-color: #fff;
    position: relative;
    margin: 0 auto 4rem;
    height: 20vw;
    .slider {
        transition-property: transform;
        box-sizing: content-box;
        overflow: hidden;
        display: flex;

        .slider__item {
            border-right: 0.5px solid #bed7ee;
            display: inline-block;
            position: relative;
            // width: $size__item;
            gap: 0.01rem;
            flex-wrap: wrap;
            justify-content: center;
            background-color: #fff;
            transition: 0.5s;

            img {
                width: 22.5vw;
                // background-repeat:space;
                height: 14vw;
            }
            .slider--info {
                display: block;
                position: relative;
                padding: 2vw 0 3vw 4vw;
                padding-top: 2vw;
                border-right: 1px solid #bed7ee;

                .slider--info-title {
                    margin-bottom: 2.5vw;
                    font-size: 1.2rem;
                }
                .slider--info-text {
                    font-size: 1.6rem;
                    font-weight: 100;
                }
            }
            .slider--info:last-child {
                border-right: none !important;
            }
        }
    }
}
.slider--nav {
    display: flex;
    justify-content: center;
    margin-bottom: 6.25rem;
    .slider--nav-items {
        transition: 0.5s;
        display: inline-block;
        background-color: rgb(179, 176, 176) !important;
        width: 0.4vw;
        height: 0.4vw;
        border-radius: 50%;
        margin: 0 1rem;
    }
    .items-active {
        transition: 0.5s;
        background-color: #000 !important;
        width: 0.7vw !important;
        height: 0.7vw !important;
    }
}


@media (max-width: 767px) {
    .slider__inner {
        // max-width: 40rem !important;
        background: none !important;
        .slider__item {
            background: none !important;
            img {
                width: 45vw !important;
                height: 26vw !important;
            }
        }
        .slider--info {
            padding: 1rem 0 !important;
            .slider--info-title {
                font-size: 0.9rem !important;
            }
            .slider--info-text {
                font-size: 1.3rem !important;
            }
        }
    }
}

@media (max-width: 1048px) {
    .slider__inner {
        img {
            width: 30vw !important;
            height: 18vw !important;
        }
    }
}
