.container__inner {
    position: relative;
    cursor: pointer;
    display: grid;
    height: auto;
    background-color: rgb(190, 215, 238);
    z-index: 3;
    top: -3rem;
    margin-bottom: -4rem;
    .container__inner-info {
        padding: 6rem 0 0;
        margin-bottom: 5rem;
        height: auto;
        .container__header__top{
            padding-left: 4rem;
            padding-top: 2rem;
            font-size: 1.8rem;
            font-weight: 900;
        }
        .container__header__midle{
             padding-left: 4rem;
            margin-top: 4rem;
            font-size: 0.8rem;
            font-weight: 200;
        }
        .container__header__bottom{
             padding-left: 4rem;
            margin: 4rem auto; 
            font-weight: 600;
            span{
                border:rgb(202, 202, 202) 2px solid;
            border-radius: 10vw;
            padding: 0.5vw 2vw;
            }
        }
    }
    .container__goods__header {
        height: 5rem;
        display: flex;
        margin: auto 4rem;
        padding-left: 3vw;
        align-items: center;
        background-color: #fff;
        gap: 2vw;
    }
    .container__goods {
        background-color: #fff;
        padding: 0 2rem;
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(18rem, 1fr));
        margin: auto 4rem;
        border-top: 1px solid rgb(179, 176, 176);
    }
}

.goods__item__cart {
    margin: 0 auto;
    padding: 1rem;
    height: 27rem;
    width: 16rem;
    transition: 500ms all;
    .cart__header {
        display: flex;
        height: 10rem !important;
        vertical-align: bottom;
        align-items: flex-start;
        flex-direction: column;
        justify-content: space-between;
        .cart__header-img {
            display: block;
            background-repeat: no-repeat;
            width: 13rem;
        }
    }
    .cart__title,
    .cart__info {
        margin-top: 2rem;

        .cart__info-price::after {
            padding-left: 0.3rem;
            content: "₽";
        }
        .cart__info-volume {
            padding-left: 0.5rem;
            font-size: 0.8rem;
            color: rgb(179, 176, 176);
        }
    }
    .cart__rating {
        height: 1rem;
        margin-top: 3rem;
        display: flex;
        .cart__rating-star {
            height: 0.8rem !important;
        }
    }
}

.cart__add-color {
    z-index: 3;
    display: none;
}
.cart__add-order ,
.cart__like-order{
    position: absolute;
    width: 4rem;
    margin: 1rem 1rem;
    border-radius: 50%;
    z-index: 1;
    height: 4rem;
    display: none;
}
.cart__like-order{
    top: 1rem !important;
}
.cart__add-order{
    background-color: #6ab04c;
}
.goods__item__cart:hover {
    box-shadow: 11px 0px 40px -20px rgba(123, 124, 126, 0.25), -11px 0px 40px -20px rgba(123, 124, 126, 0.25),
        0px 11px 40px -20px rgba(123, 124, 126, 0.25), 0px -11px 40px -20px rgba(123, 124, 126, 0.25);
    .cart__add-order {
        display: flex !important;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .cart__like-order:hover{
        background-color: rgb(238, 238, 238);
    }
    .cart__like-order{
    position: relative !important; 
    display: flex !important;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    left: 5rem;
    top: 2rem;
    border:rgb(238, 238, 238) 1px solid;
    }
    .cart__add-order:hover .cart__add-color {
        display: flex !important;
        position: absolute;
        z-index: 6;
        border-radius: 50%;
        top: 0;
        height: 4rem;
        width: 4rem;
        background-color: #85b857 !important;
        animation: 300ms identifier ease-in-out;
    }
}
@keyframes identifier {
    from {
        top: 0;
        width: 0rem;
        height: 0 !important;
    }
    1% {
        height: 0.1rem;
    }
    50% {
        height: 1rem;
    }
    to {
        top: 0;
        height: 4rem !important;
        width: 4rem;
        background-color: #85b857 !important;
    }
}

.hit,
.lean {
    position: relative;
    color: #fff;
    font-size: 12px;
    cursor: pointer;
    padding: 0.2rem 0.5rem;
}
.hit {
    background-color: rgb(99, 36, 36);
}
.lean {
    background-color: rgb(46, 109, 46);
}

@media (max-width: 768px) {
    .container__inner-info{
        margin-top: 2rem;
    }
    .pagination .active{
        padding: 1vw 2vw !important;
    }
    .cart__like-order{
        top: -6rem !important;
    }
    .container__goods__header {
        margin: 0 !important;
    }
    .container__goods {
        padding: 0 !important;
        margin: 0 !important;
        grid-template-columns: repeat(auto-fill, minmax(42vw, 1fr)) !important;
    }
    svg[AttributeStyle] {
        width: 7vw !important;
        height: 7vw !important;
    }
    .cart__info,
    .cart__rating {
        margin-top: 4vw !important;
    }
    .cart__header-img {
        width: 40vw !important;
    }
    .goods__item__cart {
        width: 42vw !important;
        margin: 0 !important;
        padding: 2vw 4vw !important;
        height: max-content !important;
        .cart__add-order {
            position: relative;
            display: flex !important;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
    }
    .hit, 
    .lean{
        display: flex !important;
        justify-content: center;
        padding: 0.2vw 18vw !important;
    }
}
.color_text_gray{
    color: rgb(179, 176, 176);
}
.color_text_black{
    color: #000;
}