.pagination {
    padding-bottom: 6vw !important;
    justify-content: center;

    .arrow_prev {
        margin-right: 7vw;
    }
    .arrow_next {
        margin-left: 7vw;
    }
    .active{
        padding: 1vw 1.5vw;
        color: #fff;
        border-radius: 50%;
        background-color: #000;
        transition: 500ms all;
    }
}
