.header {
    @media (max-width: 1700px) {
        .header__inner {
            display: flex;
            margin: 0 auto;
            width: 89vw;
            height: 12.5vw;
            padding: 0 1vw;
            justify-content: center;
            flex-direction: column;
            align-items: center;

            .header__top {
                height: 10.5vw;
                z-index: 1;
                display: flex;
                align-items: center;
                font-weight: 200;
                font-size: 0.8vw;
                z-index: 1;

                a .logo_img {
                    position: relative;
                    margin: auto 0;
                    align-items: center;
                    width: 9vw;
                    height: auto;
                }
                .header__top__contact {
                    margin: auto 2vw;
                    font-size: 1.3vw;
                    font-weight: 300;
                    .contact-time {
                        margin-top: 0.8vw;
                        font-size: 0.6vw;
                        color: rgb(179, 176, 176);
                    }
                }
                .header__top__feedback {
                    font-weight: 300;
                    cursor: pointer;
                    display: flex;
                    font-size: 1vw;
                    align-items: center;
                    border: 1px solid rgb(179, 176, 176);
                    height: 2.6vw;
                    width: 12vw;
                    border-radius: 2vw;
                    justify-content: center;
                    gap: 0.2vw;
                    margin: auto 2vw auto 1vw;
                }
                .header__top-navbar {
                    font-weight: 300;
                    margin-left: 2vw;
                    font-size: 1vw;
                    display: flex;
                    gap: 2vw;
                    .header__top-login {
                        margin-left: 1vw;
                    }
                }
            }

            .header__bottom {
                transition: 300ms all;
                background-color: #6ab04c;
                height: 6vw;
                z-index: 5;
                width: 89vw;
                top: 0;

                .header__bottom__inner {
                    display: flex;
                    position: relative;
                    padding: 1vw 0;
                    height: auto;
                    align-items: center;
                    margin-left: 3vw;

                    .nav__wrapper {
                        display: block;
                        position: relative;
                        align-items: center;
                        width: 15.8vw;
                        display: grid;
                        grid-template-columns: 2vw 1fr;
                        font-size: 1.2vw;

                        figure {
                            margin: 0;
                            width: 1.5vw;
                        }

                        .icon-burger {
                            display: grid;
                            row-gap: 0.188vw;

                            .line {
                                display: block;
                                height: 0.12rem;
                                width: 1rem;
                                background-color: #fff;
                            }


                            .line:nth-child(2) {
                                transition: 500ms all;
                                width: 0.725rem;
                                height: 0.12rem ;
                            }
                            .line:nth-child(3){
                                display: none;
                            }   
                        }
                    }

                    .nav__wrapper:hover {
                        .line:nth-child(2) {
                            width: 1rem !important;
                            transition: 500ms all;
                        }
                    }
                    .header__form__input {
                        position: relative;
                        padding-left: 4.375vw !important;
                        height: 3.75vw;
                        width: 34vw;
                        border: none !important;
                        font-size: 1.2vw;
                        font-weight: 200 !important;
                        letter-spacing: 0.03vw;
                    }
                    .header__form__input:focus {
                        outline: none;
                    }

                    .header__form__input ~ .header__from__input--search {
                        position: relative;
                        top: 0.55vw;
                        left: -38vw;
                        z-index: 4;
                        cursor: pointer;
                        height: auto;
                    }
                }

                .header__shop {
                    border-left: 1px solid rgb(179, 176, 176);
                    margin-left: 4vw;
                    display: flex;
                    padding-left: 3vw;
                    width: 2vw;
                    height: 2vw;
                    align-items: center;
                    cursor: pointer;
                }

                .header__shop-count {
                    display: flex;
                    height: 3vw;
                    color: #fff;
                    flex-direction: column;

                    .bgc-black {
                        font-size: 0.8vw;
                        padding: 0 0.3vw;
                        background-color: #000;
                        border-radius: 50%;
                    }
                }

                .header_shop-price {
                    margin-left: 3vw;
                    font-size: 1.2vw;
                    color: #fff;
                }

                .header_shop-price::after {
                    padding-left: 0.5vw;
                    content: "₽";
                    padding-right: 0.5vw;
                }
            }
        }
    }
}

.text {
    color: #fff;
    font-size: 1.2vw;
}

.btn-search {
    border: none;
    background: none;
}

@media (max-width: 1045px) {
    .header__top {
        display: none !important;
    }
    .header__bottom {
        top: 0;
        position: fixed !important;
    }
    .header__inner {
        height: 0 !important;
    }
}
.header__bot_shut {
    padding-left: 5.5vw !important;
    padding-right: 5.5vw !important;
    position: fixed !important;
}

@media (max-width: 768px) {
    .header__bottom__inner{
        padding: 0 !important;
    }
    .header__heart{
        padding: 1rem 5vw !important;
        border-right: 1px solid rgb(179, 176, 176);
        border-left: 1px solid rgb(179, 176, 176);
    }
   .nav__wrapper{
       border-right: 1px solid rgb(179, 176, 176);
       width: auto !important;
       padding: 1.6rem 6vw 1.6rem 3vw!important;

    } 
    .header__shop{
        border-left: none !important;
        margin-left: 0 !important;
        padding: 0 5vw !important;
    }
    .header__shop-count{
        height: 6vw !important;
        .bgc-black{
            font-size: 0.65rem !important;
            padding: 0.1rem 0.3rem !important;
        }
    }
    .header__form__input{
        display: none;
    }
    .header__from__input--search{
        left: 0 !important;
        margin-bottom: 0.4rem;
        padding: 1rem 4vw !important;
       border-right: 1px solid rgb(179, 176, 176);
    }
    .line{
        height: 0.125rem !important;
        margin: 0.05rem 0 0  !important;
        width: 1.3rem !important;
    }
    .line:nth-child(3){
    display: block !important;
}
    .header__bottom{
        width: 89vw !important;
        height: 6rem!important;
    }
    .nav__wrapper{
        .text {
            display: none;
        }
    }

.header_shop-price{
    display: none;
}
}

@media (max-width: 520px) {

    .header__bottom__inner{
        justify-content: space-between !important;
        margin-right: 8vw;
        a > img{
            margin: auto 8vw auto 2vw  !important;
        }
    }
    .header__from__input--search,
    .header__heart{
        display: none !important;
    }
    .header__shop{
        border-left: 1px solid rgb(179, 176, 176) !important;
        height: 12vw !important;
        // margin-right: 8vw;
    }

}